.MovieCardContainer {
  margin: 1.5rem 1.2rem;
}

.MovieCard {
  position: relative;
}

.Image {
  border: 3px solid #eed5d5;
  border-radius: 7px;
}

.HoverInfo {
  display: none;
}

.MovieCard:hover .HoverInfo {
  border-radius: 7px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 99%;
  background-color: rgba(0, 0, 0, 0.445);
  backdrop-filter: blur(1px);
}

.MovieCard:hover .Image {
  border: 3px solid rgb(72, 69, 209);
}

.Rating {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.IconContainer {
  color: rgb(72, 69, 209);
  font-size: 1.8rem;
  text-align: center;
}

.RatingNum {
  font-size: 1.4rem;
  color: rgb(255, 255, 255);
  margin: 0;
  text-align: center;
}

.Genres {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Genre {
  font-size: 1.5rem;
  color: rgb(255, 255, 255);
  font-weight: 500;
  margin: 0;
}

.Button {
  cursor: pointer;
  border: none;
  background-color: rgb(72, 69, 209);
  width: 50%;
  height: 1.8rem;
  border-radius: 5px;
  color: #202020;
  font-size: 0.9rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.LinkMore {
  margin: 0;
  padding-bottom: 4px;
}
